import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import InterbankTransferUpdateForm from "./updateForm";
import InterbankTransferDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/interbankTransfer/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/interbank-transfers";


class UpdateInterbankTransfer extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editInterbankTransfer } = this.props;
    const { id } = this.props.match.params;
    editInterbankTransfer(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.interbankTransfer.error) {
      notification("error", this.props.interbankTransfer.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.interbankTransfer.success) {
      notification("success", this.props.interbankTransfer.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateInterbankTransfer } = this.props;
    const { id } = this.props.match.params;

    updateInterbankTransfer(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelInterbankTransfer } = this.props;
    const { id } = this.props.match.params;

    cancelInterbankTransfer(id, value);
  };

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { interbank_transfer, loading } = this.props.interbankTransfer
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="interbankTransfers.update.title" />}
            style={{ margin:0 }}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={interbank_transfer} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          
          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={interbank_transfer ? interbank_transfer.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="interbankTransfers.tab.detail" />} 
              key="1" 
            >
              {this.props.interbankTransfer.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <InterbankTransferUpdateForm
                  submit={this.handleSubmit}
                  interbankTransfer={this.props.interbankTransfer}
                  permissions={permissions}
                  processInterbankTransfer={this.props.processInterbankTransfer}
                  handleSubmitCancel={this.handleSubmitCancel}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="interbankTransfers.tab.attachment" />} 
              key="2"
            >  
             {this.props.interbankTransfer.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <InterbankTransferDocUpdateForm
                  submit={this.handleSubmit}
                  interbankTransfer={this.props.interbankTransfer}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    interbankTransfer: state.interbankTransfer, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateInterbankTransfer);
