import React, { Component } from "react";
import moment from "moment"
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, InputNumber, DatePicker, Popconfirm, Steps, Row, Divider } from "antd";
import { customDot } from "../../../App/app.style";
import ModalCancel from "./modalCancel";
import { getCurrencyRate } from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";


const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class InterbankTransferUpdateForm extends Component {
  state = {
    currency_is_base: 2,
    currency_deposit_cash_bank: null,
    currency_withdraw_cash_bank: null,
    visibleModalInterbankTransfer: false,
    visibleModalCancel: false,
  }

  componentDidUpdate = () => {
    if (this.state.currency_is_base === 2 && this.props.interbankTransfer.interbank_transfer !== null) {

      const { interbank_transfer } = this.props.interbankTransfer;
      const { setFieldsValue } = this.props.form;

      setFieldsValue({
        withdraw_id_location: interbank_transfer.withdraw_id_location,
        withdraw_id_cash_bank: interbank_transfer.withdraw_id_cash_bank,
        withdraw_id_account: interbank_transfer.withdraw_id_account,
        withdraw_id_cash_flow_type: interbank_transfer.withdraw_id_cash_flow_type,
        id_currency: interbank_transfer.id_currency,
        currency_rate: parseFloat(interbank_transfer.currency_rate),
        currency_amount: parseFloat(interbank_transfer.currency_amount),
        base_amount: parseFloat(interbank_transfer.currency_rate) * parseFloat(interbank_transfer.currency_amount),
        note: interbank_transfer.note,
        deposit_id_location: interbank_transfer.deposit_id_location,
        deposit_id_cash_bank: interbank_transfer.deposit_id_cash_bank,
        deposit_id_account: interbank_transfer.deposit_id_account,
        deposit_id_cash_flow_type: interbank_transfer.deposit_id_cash_flow_type,
      })

      this.setState({
        currency_is_base: interbank_transfer.currency.is_base
      })

    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  setFieldBaseAmount = (rate) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let amount = getFieldValue('currency_amount') ? getFieldValue('currency_amount') : 0
    let base_amount = rate * amount;

    setFieldsValue({
      ["currency_rate"]: rate,
      ["base_amount"]: base_amount,
    })
  }

  handleChangeTransDate = (e) => {
    if (e instanceof moment) {
      const { getFieldValue } = this.props.form
      if (getFieldValue("id_currency")) {
        this.props.interbankTransfer.currencies.map((x) => {
          if (x.id_currency === getFieldValue("id_currency")) {
            let rate = getCurrencyRate(x.currency_rate, e)
            this.setFieldBaseAmount(rate)
            this.setState({ currency_is_base: x.is_base })
            //console.log("")
          }
        })
      }
    }
  }


  handleChangeWithdrawCashBank = (e) => {
    this.props.interbankTransfer.cash_banks.map((data) => {
      if (data.id_cash_bank === e) {
        const { setFieldsValue, getFieldValue } = this.props.form
        setFieldsValue({
          ["id_currency"]: data.id_currency,
          ["withdraw_id_account"]: data.id_account,
        })
        this.props.interbankTransfer.currencies.map((x) => {
          if (x.id_currency === data.id_currency) {
            let rate = getCurrencyRate(x.currency_rate, getFieldValue("transaction_date"))
            this.setFieldBaseAmount(rate)
            this.setState({ currency_is_base: x.is_base })
          }
        })
        this.setState({ currency_withdraw_cash_bank: data.id_currency })
      }
    })
  }


  handleChangeDepositCashBank = (e) => {
    this.props.interbankTransfer.cash_banks.map((data) => {
      if (data.id_cash_bank === e) {
        const { setFieldsValue } = this.props.form
        setFieldsValue({
          ["deposit_id_account"]: data.id_account,
        })
        this.setState({ currency_deposit_cash_bank: data.id_currency })
      }
    })
  }

  handleChangeBaseAmountFromRate = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let rate = e ? e : 1
    let amount = getFieldValue('currency_amount') ? getFieldValue('currency_amount') : 0
    let base_amount = rate * amount;

    this.setState({
      currency_rate: rate
    })

    setFieldsValue({
      ["base_amount"]: base_amount,
    })

  }

  handleChangeBaseAmountFromAmount = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let rate = getFieldValue('currency_rate') ? getFieldValue('currency_rate') : 0
    let amount = e ? e : 0
    let base_amount = rate * amount;

    setFieldsValue({
      ["base_amount"]: base_amount,
    })

  }

  handleChangeWithdrawLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      withdraw_id_cash_bank: null
    })
    this.setState({ currency_withdraw_cash_bank: null })
  }

  handleChangeDepositLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      deposit_id_cash_bank: null
    })
    this.setState({ currency_deposit_cash_bank: null })
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        this.props.submit(values);
      }
    });
  };




  render() {
    // console.log(this.state);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { interbankTransfer, processInterbankTransfer } = this.props;
    const { loading, cash_flow_types } = interbankTransfer;

    const data = interbankTransfer.interbank_transfer ? interbankTransfer.interbank_transfer : [];

    let permissions = this.props.permissions;

    // const error = interbankTransfer.errorField ? interbankTransfer.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="interbankTransfers.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status - 1
              }
            >
              <Step title={getLanguage({id: "open"})} />
              <Step title={getLanguage({id: "processed"})} />
              <Step title={getLanguage({id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="interbankTransfers.no" />}
            >
              {getFieldDecorator("interbank_transfer_no", {
                initialValue: data.interbank_transfer_no,

              })(<Input
                disabled={true}
              />)}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="purchaseOrders.transactionDate" />
              }
            >
              {getFieldDecorator("transaction_date", {
                initialValue: moment(data.transaction_date, "YYYY-MM-DD HH:mm:ss"),
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  showTime
                  format="DD MMMM YYYY HH:mm:ss"
                  onChange={this.handleChangeTransDate}
                  onOpenChange={this.handleChangeTransDate}
                  disabled={
                    permissions.includes("Can Back Date") && data.status === 1
                      ? false
                      : true
                  }
                  allowClear={false}
                  disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="interbankTransfers.add.subtitle.withdraw" />
            </Divider>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.withdrawLocation" />}
                >
                  {getFieldDecorator("withdraw_id_location", {
                    // initialValue: data.withdraw_id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeWithdrawLocation}
                      style={{ width: "100%" }}
                      disabled={data.status !== 1 ? true : false}
                    >
                      {interbankTransfer.withdraw_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.withdrawCashBank" />}
                >
                  {getFieldDecorator("withdraw_id_cash_bank", {
                    // initialValue: data.withdraw_id_cash_bank,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      // allowClear={true}
                      optionFilterProp="children"
                      onChange={this.handleChangeWithdrawCashBank}
                      style={{ width: "100%" }}
                      disabled={data.status !== 1 ? true : false}
                    >
                      {
                        getFieldValue('withdraw_id_location')
                          ? interbankTransfer.cash_banks.map((data, key) => (
                            getFieldValue('withdraw_id_location') === data.id_location &&
                              getFieldValue('deposit_id_cash_bank') !== data.id_cash_bank && (!this.state.currency_deposit_cash_bank || this.state.currency_deposit_cash_bank === data.id_currency)
                              ?
                              (
                                <Option key={key} value={data.id_cash_bank}>
                                  {data.bank_account_number === null
                                    ? data.cash_bank_code
                                    : data.cash_bank_code + " (" + data.bank_account_number + ")"
                                  }
                                </Option>
                              )
                              : null
                          ))
                          : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.withdrawAccount" />}
                >
                  {getFieldDecorator("withdraw_id_account", {
                    // initialValue: data.withdraw_id_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      // onChange={this.handleChangeAccount.bind(this)}
                      // onOpenChange={this.handleChangeGLAccount}
                      disabled={true}
                    >

                      {getFieldValue('withdraw_id_cash_bank')
                        ?
                        interbankTransfer.accounts.map((data, key) => (
                          // (data.id_account_type !== 1 || data.id_account_type !== 3) && data.sub_ledger_type === 0
                          // ? 
                          (
                            <Option key={key} value={data.id_account}>
                              {data.account_code + " (" + data.account_name + ")"}
                            </Option>
                          )
                          // : null
                        ))
                        : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.withdrawCashFlowType" />}
                >
                  {getFieldDecorator("withdraw_id_cash_flow_type", {
                    //initialValue: data.withdraw_id_cash_flow_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ],
                  })(
                    <Select
                      //allowClear
                      showSearch
                      style={{ width: "100%" }}
                      optionFilterProp="children"
                      disabled={data.status !== 1 ? true : false}
                    >
                      {cash_flow_types.map((data) => (
                        data.transaction_type === 2
                          ? (
                            <Option key={data.id_cash_flow_type} value={data.id_cash_flow_type}>
                              {data.cash_flow_type_code + " (" + data.description + ")"}
                            </Option>
                          )
                          : null
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="currencies.pageHeader" />}
                >
                  {getFieldDecorator("id_currency", {
                    // initialValue: data.id_currency,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={true}
                      style={{ width: "100%" }}
                    >
                      {interbankTransfer.currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="rate" />}
                >
                  {getFieldDecorator("currency_rate", {
                    //initialValue: this.state.currency_is_base===1 ? 1 : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0.01}
                      precision={2}
                      disabled={this.state.currency_is_base === 0 && data.status === 1 ? false : true}
                      //disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="amount" />}
                >
                  {getFieldDecorator("currency_amount", {
                    // initialValue: this.state.currency_is_base===1 ? 1 : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0.01}
                      precision={2}
                      disabled={data.status !== 1 ? true : false}
                      style={{ width: "100%" }}
                      onChange={this.handleChangeBaseAmountFromAmount}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="baseAmount" />}
                >
                  {getFieldDecorator("base_amount", {
                    //initialValue: data.base_amount,
                    // initialValue: 0, 
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={0.01}
                      precision={2}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.description,
              })(<TextArea
                autosize={{ minRows: 4 }}
                style={{ marginBottom: "8px" }}
                disabled={data.status !== 1 ? true : false}
              />)}
            </Form.Item>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="interbankTransfers.add.subtitle.deposit" />
            </Divider>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.depositLocation" />}
                >
                  {getFieldDecorator("deposit_id_location", {
                    // initialValue: data.deposit_id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeDepositLocation}
                      style={{ width: "100%" }}
                      disabled={data.status !== 1 ? true : false}
                    >
                      {interbankTransfer.deposit_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.depositCashBank" />}
                >
                  {getFieldDecorator("deposit_id_cash_bank", {
                    // initialValue: data.deposit_id_cash_bank,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      // allowClear={true}
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      onChange={this.handleChangeDepositCashBank}
                      disabled={data.status !== 1 ? true : false}
                    >
                      {
                        getFieldValue('deposit_id_location')
                          ? interbankTransfer.cash_banks.map((data, key) => (
                            getFieldValue('deposit_id_location') === data.id_location &&
                              getFieldValue('withdraw_id_cash_bank') !== data.id_cash_bank && (!this.state.currency_withdraw_cash_bank || data.id_currency === this.state.currency_withdraw_cash_bank)
                              ?
                              (
                                <Option key={key} value={data.id_cash_bank}>
                                  {data.bank_account_number === null
                                    ? data.cash_bank_code
                                    : data.cash_bank_code + " (" + data.bank_account_number + ")"
                                  }
                                </Option>
                              )
                              : null
                          ))
                          : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.depositAccount" />}
                >
                  {getFieldDecorator("deposit_id_account", {
                    // initialValue: data.deposit_id_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.interbankTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      // onChange={this.handleChangeAccount.bind(this)}
                      // onOpenChange={this.handleChangeGLAccount}
                      disabled={true}
                    >

                      {getFieldValue('deposit_id_cash_bank')
                        ?
                        interbankTransfer.accounts.map((data, key) => (
                          // (data.id_account_type !== 1 || data.id_account_type !== 3) && data.sub_ledger_type === 0
                          // ? 
                          (
                            <Option key={key} value={data.id_account}>
                              {data.account_code + " (" + data.account_name + ")"}
                            </Option>
                          )
                          // : null
                        ))
                        : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="interbankTransfers.depositCashFlowType" />}
                >
                  {getFieldDecorator("deposit_id_cash_flow_type", {
                    //initialValue: data.deposit_id_cash_flow_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ],
                  })(
                    <Select
                      //allowClear
                      showSearch
                      style={{ width: "100%" }}
                      optionFilterProp="children"
                      disabled={data.status !== 1 ? true : false}
                    >
                      {cash_flow_types.map((data) => (
                        data.transaction_type === 1
                          ? (
                            <Option key={data.id_cash_flow_type} value={data.id_cash_flow_type}>
                              {data.cash_flow_type_code + " (" + data.description + ")"}
                            </Option>
                          )
                          : null
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
        </div>


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processInterbankTransfer(data.id_interbank_transfer)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Interbank Transfer")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Interbank Transfer")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedInterbankTransferUpdateForm = Form.create({ name: "update_interbank_transfer" })(InterbankTransferUpdateForm);

export default WrappedInterbankTransferUpdateForm;
